}

body {
  color: #646464;
  font-family: "Poppins";
  line-height: 1.8;
  padding: 0;
  margin: 0;
  font-size: 15px;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  display: inline-block;
  transition: 0.5s;
}
a:hover {
  text-decoration: none;
}

ul li {
  display: inline-block;
}

.d-table {
  width: 100%;
  height: 100%;
}

.d-table-cell {
  display: table-cell;
  vertical-align: middle;
}

p {
  color: #646464;
  line-height: 1.8;
}

i {
  line-height: 1;
}

h3, .h3 {
  font-size: 20px;
  font-weight: 600;
}

.form-control:focus, input:focus, button:focus, .btn:focus {
  outline: 0;
  box-shadow: 0 0 0 0 !important;
}

.ptb-100 {
  padding-top: 100px;
  padding-bottom: 100px;
}

.pt-100 {
  padding-top: 100px;
}

.pb-70 {
  padding-bottom: 70px;
}

.mr-20 {
  margin-right: 20px;
}

.pl-40 {
  padding-left: 40px;
}

.theme-button .default-btn {
  font-size: 15px;
  color: #ffffff;
  font-weight: 500;
  padding: 15px 35px;
  border-radius: 5px;
  margin-right: 25px;
  position: relative;
  text-transform: capitalize;
  border: 1px solid #ffffff;
  z-index: 0;
}
.theme-button .default-btn::before {
  position: absolute;
  content: "";
  width: 0;
  height: 100%;
  left: 0;
  top: 0;
  z-index: -1;
  transition: 0.5s;
  border-radius: 5px;
  background: #6096fd;
}
.theme-button .default-btn:hover::before {
  width: 100%;
  height: 100%;
}
.theme-button .default-btn:hover {
  border: 1px solid transparent;
}

.section-head {
  max-width: 600px;
  margin: -1px auto 55px;
  line-height: 1;
}
.section-head span {
  font-size: 18px;
  font-weight: 500;
  color: #fb788e;
  margin-bottom: 10px;
  display: inline-block;
}
.section-head h2, .section-head .h2 {
  font-size: 40px;
  color: #111111;
  font-weight: 600;
  margin-bottom: 20px;
  line-height: 1;
}
.section-head h2 span, .section-head .h2 span {
  color: #6096fd;
  font-weight: 600;
  font-size: 40px;
  margin-bottom: 0;
}
.section-head p {
  margin-bottom: 0;
}

.blue-title h2 span, .blue-title .h2 span {
  color: #031888;
  font-size: 40px;
  font-weight: 600;
}

/*------------ HOME PAGE ONE CSS START -------------*/
/* Header CSS Start */
.header-section {
  background: #031888;
  padding: 10px 0;
}
.header-section .header-widget ul {
  padding-left: 0;
  margin-bottom: 0;
}
.header-section .header-widget ul li {
  font-size: 14px;
  padding-right: 16px;
  margin-right: 10px;
  color: #ffffff;
  position: relative;
}
.header-section .header-widget ul li::before {
  position: absolute;
  content: "";
  background: #ffffff;
  width: 1px;
  height: 20px;
  right: 0;
  top: 3px;
}
.header-section .header-widget ul li i {
  margin-right: 5px;
  font-size: 18px;
  top: 1px;
  position: relative;
}
.header-section .header-widget ul li a {
  color: #ffffff;
}
.header-section .header-widget ul :nth-child(3)::before {
  display: none;
}
.header-section .header-social ul {
  padding-left: 0;
  margin-bottom: 0;
}
.header-section .header-social ul li {
  margin-left: 10px;
}
.header-section .header-social ul li a i {
  color: #ffffff;
  background: #1d3094;
  width: 30px;
  height: 30px;
  border-radius: 5px;
  display: inline-block;
  text-align: center;
  line-height: 30px;
  position: relative;
  z-index: 0;
  transition: 0.5s;
}
.header-section .header-social ul li a i::after {
  position: absolute;
  content: "";
  width: 0;
  height: 0;
  background: #ffffff;
  top: 0;
  left: 0;
  border-radius: 5px;
  z-index: -1;
  transition: 0.5s;
}
.header-section .header-social ul li a i:hover::after {
  width: 100%;
  height: 100%;
}
.header-section .header-social ul li a i:hover {
  color: #6096fd;
}

/* Header CSS End */
/* Navbar CSS Start */
.navbar-area .main-nav {
  background: #ffffff;
  background-image: url(assets/img/headerbg.png);
  padding: 0;
  position: relative;
  transition: 0.5s;
}
.navbar-area .main-nav .navbar {
  padding: 15px 0;
}
.navbar-area .main-nav .navbar .navbar-nav .nav-item a {
  color: #ffffff;
  font-weight: 500;
  text-transform: capitalize;
}
.navbar-area .main-nav .navbar .navbar-nav .nav-item a:hover {
  color: #031888;
}
.navbar-area .main-nav .navbar .navbar-nav .nav-item a:hover i {
  display: inline-block;
  transform: rotate(90deg);
}
.navbar-area .main-nav .navbar .navbar-nav .nav-item a i {
  top: 1px;
  position: relative;
  transition: 0.5s;
}
.navbar-area .main-nav .navbar .navbar-nav .nav-item .active {
  color: #ffffff !important;
}
.navbar-area .main-nav .navbar .navbar-nav .dropdown-toggle::after {
  display: none;
}
.navbar-area .main-nav .navbar .navbar-nav .dropdown-menu {
  background: #ffffff;
  transition: 0.5s;
  border: 0;
  box-shadow: 7px 5px 30px rgba(72, 73, 121, 0.15);
  padding: 0;
}
.navbar-area .main-nav .navbar .navbar-nav .dropdown-menu .nav-item {
  display: block;
  padding: 0;
}
.navbar-area .main-nav .navbar .navbar-nav .dropdown-menu .nav-item a {
  color: #282828;
  border-bottom: 1px dashed #ccb0b0;
  position: relative;
}
.navbar-area .main-nav .navbar .navbar-nav .dropdown-menu .nav-item a::before {
  position: absolute;
  content: "";
  background: #6096fd;
  width: 3px;
  height: 0;
  right: 0;
  bottom: 0;
  transition: 0.5s;
}
.navbar-area .main-nav .navbar .navbar-nav .dropdown-menu .nav-item a::after {
  position: absolute;
  content: "";
  background: #6096fd;
  width: 3px;
  height: 95%;
  left: 0;
  bottom: 0;
  transition: 0.5s;
}
.navbar-area .main-nav .navbar .navbar-nav .dropdown-menu .nav-item a:hover {
  color: #031888;
}
.navbar-area .main-nav .navbar .navbar-nav .dropdown-menu .nav-item a:hover::before {
  height: 95%;
}
.navbar-area .main-nav .navbar .navbar-nav .dropdown-menu .nav-item a:hover::after {
  height: 0;
}
.navbar-area .main-nav .navbar .navbar-nav .dropdown-menu .nav-item a .active {
  color: #031888;
}
.navbar-area .main-nav .navbar .navbar-nav .dropdown-menu :last-child a {
  border-bottom: 0;
}
.navbar-area .main-nav .navbar .text-color {
  color: #0976CB;
}
.navbar-area .main-nav .navbar .navbar-button {
  margin-left: 25px;
}
.navbar-area .main-nav .navbar .navbar-button a {
  color: #ffffff;
  background: #0976CB;
  padding: 10px 37px;
  border-radius: 5px;
  position: relative;
  z-index: 0;
  font-size: 16px;
}
.navbar-area .main-nav .navbar .navbar-button a::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 0;
  background: #031888;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  border-radius: 5px;
  z-index: -1;
  transition: 0.5s;
}
.navbar-area .main-nav .navbar .navbar-button a:hover::before {
  height: 100%;
}

.is-sticky {
  position: fixed !important;
  top: 0 !important;
  left: 0;
  width: 100%;
  z-index: 999;
  box-shadow: 0 2px 28px 0 rgba(0, 0, 0, 0.06);
  background: #ffffff !important;
  animation: 500ms ease-in-out 0s normal none 1 running fadeInDown;
  transition: 0.6s;
}

/* Navbar CSS End */
/* Home CSS Start */
.home-slider-item {
  background-position: center center;
  background-size: cover;
  height: 100vh;
  position: relative;
}
.home-slider-item::before {
  position: absolute;
  content: "";
  background: linear-gradient(90deg, rgba(3, 24, 136, 0.8463760504) 20%, rgba(0, 0, 0, 0) 100%);
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}

.home-slider-area {
  position: relative;
}

.home-slider-area.owl-carousel .owl-nav button.owl-prev,
.home-slider-area.owl-carousel .owl-nav button.owl-next {
  position: absolute;
  left: 25px;
  top: 35%;
  width: 55px;
  height: 55px;
  border-radius: 30px;
  font-size: 25px;
  color: #ffffff;
  transition: 0.5s;
  background: rgba(0, 0, 0, 0.35);
}
.home-slider-area.owl-carousel .owl-nav button.owl-prev:hover,
.home-slider-area.owl-carousel .owl-nav button.owl-next:hover {
  background: #6096fd;
}

.home-slider-area.owl-carousel .owl-nav button.owl-next {
  left: unset;
  right: 25px;
}

.home-slider-area .owl-dots {
  position: absolute;
  bottom: 40px;
  left: 0;
  right: 0;
}

.home-slider-area.owl-theme .owl-dots .owl-dot.active span,
.home-slider-area.owl-theme .owl-dots .owl-dot:hover span {
  background: #ffffff;
  position: relative;
}
.home-slider-area.owl-theme .owl-dots .owl-dot.active span::before,
.home-slider-area.owl-theme .owl-dots .owl-dot:hover span::before {
  position: absolute;
  content: "";
  width: 25px;
  height: 25px;
  left: -5px;
  right: 0px;
  top: -5px;
  border-radius: 20px;
  background: transparent;
  border: 1px solid #ffffff;
}

.home-slider-area.owl-theme .owl-dots .owl-dot span {
  width: 15px;
  height: 15px;
  background: #6096fd;
}

.home-slider-area.owl-theme .owl-nav {
  margin-top: 0;
}

.items-bg1 {
  background-image: url(assets/img/home/1.png);
}

.items-bg2 {
  background-image: url(assets/img/home/2.png);
}

.items-bg3 {
  background-image: url(assets/img/home/3.png);
}

.home-text {
  max-width: 740px;
  z-index: 1;
  position: relative;
}
.home-text h1, .home-text .h1 {
  font-size: 70px;
  font-weight: 700;
  margin-bottom: 40px;
  text-transform: capitalize;
  color: #ffffff;
}
.home-text p {
  color: #ffffff;
  margin-bottom: 50px;
}
.home-text .active-btn {
  background: #6096fd;
  border: 1px solid transparent;
}
.home-text .active-btn::before {
  background: transparent;
}
.home-text .active-btn:hover {
  background: transparent;
  border: 1px solid #ffffff;
}

/* Home CSS End */
/* Service CSS Start */
.service-section {
  position: relative;
}
.service-section .service-shapes img {
  position: absolute;
  animation: translate infinite 2s;
}
.service-section .service-shapes :nth-child(1) {
