  right: 3%;
  animation-delay: 0.3s;
}
.service-section .service-shapes :nth-child(10) {
  bottom: 10%;
  right: 3%;
}

.service-card {
  text-align: center;
  border: 1px solid #c3ccff;
  border-radius: 10px;
  padding: 35px 30px;
  transition: 0.6s;
  position: relative;
  z-index: 0;
  margin-bottom: 30px;
}
.service-card:hover {
  box-shadow: 7px 5px 30px rgba(72, 73, 121, 0.15);
  transition: 0.6s ease-in-out;
}
.service-card:hover::before {
  height: 100%;
}
.service-card:hover::after {
  opacity: 1;
}
.service-card:hover i, .service-card:hover h3, .service-card:hover .h3, .service-card:hover p {
  color: #ffffff;
}
.service-card:hover i::after {
  transform: scale(1);
}
.service-card:hover .theme-button .default-btn {
  background: #ffffff;
}
.service-card::before {
  position: absolute;
  content: "";
  background: #6096fd;
  width: 100%;
  height: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  border-radius: 10px;
  transition: 0.6s;
}
.service-card::after {
  position: absolute;
  content: "";
  background-image: url(assets/img/service/shapes/service-pattern-1.png);
  width: 75px;
  height: 50px;
  top: 0;
  left: 0;
  right: 0;
  margin: auto;
  opacity: 0;
  transition: 0.6s;
}
.service-card i {
  color: #6096fd;
  font-size: 50px;
  display: inline-block;
  transition: 0.6s;
  position: relative;
  z-index: 0;
  margin-bottom: 20px;
}
.service-card h3, .service-card .h3 {
  color: #111111;
  transition: 0.6s;
  margin-bottom: 10px;
}
.service-card p {
  transition: 0.6s;
  margin-bottom: 20px;
}
.service-card .theme-button .default-btn {
  color: #6096fd;
  border: 1px solid #7ca4f1;
  margin-right: 0;
  padding: 10px 40px;
}
.service-card .theme-button .default-btn:hover::before {
  display: none;
  color: #6096fd;
}

.active-service {
  box-shadow: 7px 5px 30px rgba(72, 73, 121, 0.15);
}
.active-service::before {
  height: 100%;
}
.active-service::after {
  opacity: 1;
}
.active-service i, .active-service h3, .active-service .h3, .active-service p {
  color: #ffffff;
}
.active-service i::after {
  transform: scale(1);
}
.active-service .theme-button .default-btn {
  background: #ffffff;
}

/* Service CSS End */
/* Why Choose CSS Start */
.why-choose-section {
  z-index: 1;
  position: relative;
  padding: 0px 0px 0px 0px;
}
.why-choose-section::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  background-color: white;
  opacity: 0.8;
}
.why-choose-section .why-choose-shape {
  position: absolute;
  left: 35%;
  top: 180px;
  animation: scale infinite 2s;
}

.why-choose-bg {
  background-color: white;
  background-position: center center;
  background-size: cover;
  position: relative;
  height: 100%;
}

.why-choose-img {
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
  height: 750px;
  max-width: 650px;
