}

.why-choose-text .section-head {
  margin: 0 0 35px;
}
.why-choose-text .section-head h2, .why-choose-text .section-head .h2 {
  color: #ffffff;
  margin-bottom: 30px;
  margin-top: -4px;
}
.why-choose-text .section-head p {
  color: #ffffff;
  padding-right: 90px;
}

.why-choose-accordian {
