/* Why Choose CSS End */
/* Process CSS Start */
.process-section {
  padding-top: 160px;
}
.process-section .process-card {
  text-align: center;
  position: relative;
  margin-bottom: 30px;
  padding: 20px 20px 0;
}
.process-section .process-card:hover i {
  color: #ffffff;
}
.process-section .process-card:hover i::after {
