/* Price CSS Start */
.price-card-area {
  text-align: center;
  border: 1px solid #c3ccff;
  border-radius: 5px;
  transition: 0.6s;
  margin-bottom: 30px;
}
.price-card-area:hover .price-head-text::before {
  opacity: 1;
}
.price-card-area:hover .plan-btn a::before {
  width: 100%;
}
.price-card-area:hover .plan-btn a {
  color: #ffffff;
}
.price-card-area:hover {
  box-shadow: 7px 5px 30px rgba(72, 73, 121, 0.15);
}
.price-card-area .price-head-text {
  background: #6096fd;
  border-radius: 5px 5px 0 0;
  position: relative;
  z-index: 0;
  padding: 20px;
}
.price-card-area .price-head-text::before {
  position: absolute;
  content: "";
  background-image: url(assets/img/price-shape.png);
  background-position: center center;
  background-size: cover;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  z-index: -1;
  transition: 1s ease-in-out;
  opacity: 0;
}
.price-card-area .price-head-text h4, .price-card-area .price-head-text .h4 {
  font-size: 17px;
  color: #ffffff;
  font-weight: 500;
  margin-bottom: 10px;
}
.price-card-area .price-head-text h2, .price-card-area .price-head-text .h2 {
  font-size: 30px;
  color: #ffffff;
  font-weight: 500;
  margin-bottom: 0;
}
.price-card-area .price-head-text h2 span, .price-card-area .price-head-text .h2 span {
  font-size: 36px;
  color: #ffffff;
  font-weight: 500;
}
.price-card-area .plan-features {
  padding: 30px 30px 30px 90px;
}
.price-card-area .plan-features ul {
  text-align: left;
  margin-bottom: 0;
  padding-left: 0;
}
